.ToDoCounter {
    font-size: 24px;
    text-align: center;
    margin: 0;
    padding: 48px;
    color:#503459;
}

.ToDoSearch {
    background: white;
    border-radius: 4px;
    border: 2px solid #503459;
    margin: 0 24px;
    height: 50px;
    width: calc(100% - 62px);
    font-size: 18px;
    text-align: center;
    font-family: 'Montserrat';
    font-weight: 400;
    color:  #503459;
    box-shadow: 0px 5px 25px rgba(179, 97, 250, 0.5);
}

.ToDoSearch::-webkit-input-placeholder {
    color: #dac9df;
    font-family: 'Montserrat';
    font-weight: 400;
}

.ToDoSearch::placeholder {
    color: #dac9df;
    font-family: 'Montserrat';
    font-weight: 400;
}

.ToDoSearch:focus {
    outline-color:#b695c0;
}
ul {
    margin: 0;
    padding: 0 0 56px 0;
    list-style: none;

}

.Icon-container {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
    font-size: 24px;
    font-weight: bold;
    color:#503459;
}
.Icon-container--check{
    position: absolute;
    left: 12px;
}
.Icon-container--delete{
    position: absolute;
    top: 12px;
    right: 0;
}
.Icon-svg {
    width: 24px;
    height: 24px;
    transition: fill .15s;
}
.Icon-container:hover .Icon-svg--check {
    fill: #4caf50;
}

.Icon-container:hover .Icon-svg--delete {
    fill: red;
}
.TodoItem {
  background-color: #dac9df;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 24px;
  border-radius: 4px;
  box-shadow: 0px 5px 25px rgba(179, 97, 250, 0.5);
}

.TodoItem-p {
  margin: 24px 0 24px 24px;
  width: calc(100% - 120px);
  font-size: 18px;
  line-height: 24px;
  font-weight: 400;
  color:#503459;
  
}
.TodoItem-p--complete {
  text-decoration: line-through;
}
form {
  width: 90%;
  max-width: 300px;
  background-color: #fff;
  border-radius: 6px;
  padding: 33px 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  
}

label {
  text-align: center;
  font-weight: bold;
  font-size: 20px;
  color: #503459;
  margin-bottom: 26px;
}

textarea {
  background-color: #F9FBFC;
  border: 2px solid #503459;
  border-radius: 4px;
  box-shadow: 0px 5px 25px rgba(179, 97, 250, 0.5);
  color: #81638b;
  font-size: 20px;
  text-align: center;
  padding: 12px;
  height: 96px;
  width: calc(100% - 25px);
  resize: vertical;
}

textarea::-webkit-input-placeholder {
  color: #A5A5A5;
  font-family: 'Montserrat';
  font-size: 16px;
}

textarea::placeholder {
  color: #A5A5A5;
  font-family: 'Montserrat';
  font-size: 16px;
}

textarea:focus {
  outline-color: #b695c0;
}

.TodoForm-buttonContainer {
  margin-top: 14px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.TodoForm-button {
  cursor: pointer;
  display: inline-block;
  font-size: 18px;
  color: #503459;
  font-weight: bold;
  width: 120px;
  height: 48px;
  border-radius: 4px;
  border: none;
  font-family: 'Montserrat';
}

.TodoForm-button--add {
  background: #b695c0;
  box-shadow: 0px 5px 25px rgba(179, 97, 250, 0.5);
}

.TodoForm-button--cancel {
  background: transparent;
}
.CreateToDoButton {
    background-color: #81638b;
    box-shadow: 0px 5px 25px rgba(179, 97, 250, 0.5);
    border: none;
    border-radius: 50%;
    cursor: pointer;
    font-size: 50px;
    position: fixed;
    bottom: 24px;
    right: 24px;
    font-weight: bold;
    color: #ffffff;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 60px;
    width: 60px;
    -webkit-transform: rotate(0);
            transform: rotate(0);
    transition: .3s ease;
    z-index: 2; 
  }
  
  .CreateToDoButton:hover {
    -webkit-transform: rotate(224deg);
            transform: rotate(224deg);
  }
.ModalBackground {
    background: rgba(32,35,41,.8);
    position: fixed;
    top: -10px;
    left: -10px;
    right: -10px;
    bottom: -10px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
  }
.LoadingTodo-Container{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    box-shadow: 0px 5px 25px rgba(179, 97, 250, 0.5);
}
.LoadingTodo-completeIcon,
.LoadingTodo-deleteIcon{
    border-radius: 50px;
    height: 48px;
    width: 48px;
}
.LoadingTodo-completeIcon{
    position: absolute;
    left: 12px;
}
.LoadingTodo-deleteIcon{
    position: absolute;
    top: -24px;
    right: 0;
}
.LoadingTodo-Container,
.LoadingTodo-completeIcon,
.LoadingTodo-deleteIcon{
    background: rgb(80,52,89);
    background: linear-gradient(90deg, rgba(80,52,89,1) 0%, rgba(182,149,192,1) 35%, rgba(218,201,223,0.27494747899159666) 100%);
}

@-webkit-keyframes loadingAnimation {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}

@keyframes loadingAnimation {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}
.EmptyToDos-text{
    text-align: center;
    margin-top: 48px;
}
body {
  margin: 0;
  padding: 0;
  background: #ffffff;
  color:#81638b;
  font-family: 'Montserrat', Arial, Helvetica, sans-serif;
}

#root {
  margin: 0 24px;
  position: relative;
  min-height: 100vh;
}
