.TodoItem {
  background-color: #dac9df;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 24px;
  border-radius: 4px;
  box-shadow: 0px 5px 25px rgba(179, 97, 250, 0.5);
}

.TodoItem-p {
  margin: 24px 0 24px 24px;
  width: calc(100% - 120px);
  font-size: 18px;
  line-height: 24px;
  font-weight: 400;
  color:#503459;
  
}
.TodoItem-p--complete {
  text-decoration: line-through;
}